@import url(https://fonts.googleapis.com/css?family=Codystar|Megrim|Ubuntu:200,300,400,700|Playfair+Display:900|Hind:300&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.h-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.h-name {
    font-size: 56px;
    font-family: Playfair Display;
}

.h-rname {
    font-size: 24px;
    font-family: Playfair Display;
    margin: 16px 0 24px 0;
    font-style: italic;
}

.h-subtitle {
    font-family: Hind;
    font-size: 18px;
    width: 90%;
    font-weight: 100;
}

.h-particles {
    position: fixed;
    right: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
}

@media screen and (min-width: 720px) {
    .h-name {
        font-size: 128px;
    }
    .h-rname {
        font-size: 32px;
    }
    .h-subtitle {
        font-size: 25px;
        width: 60%;
    }
}
.r-container {
    display: flex;
    /* align-items: center; */
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    height: 100%;
}

.r-timeline {
    height: 80%;
    margin-left: 20%;
    position: relative;
}

.r-line {
    width: 2px;
    background: #fff;
    height: 100%;
    position: absolute;
    z-index: -1;
}

.r-points {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    margin-left: -19px;
}

.r-point {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666;
}
html,
body {
    background-color: #ccc;
    font-family: 'Ubuntu';
    width: 100%;
    max-width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
    background: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: default;
}

#root {
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

.app-container {
    -webkit-scroll-snap-type: x mandatory;
        -ms-scroll-snap-type: x mandatory;
            scroll-snap-type: x mandatory;
    display: flex;
    overflow-x: scroll;
    /* scroll-behavior: smooth; */
}

.page {
    /* cursor: pointer; */
    /* position: absolute; */
    scroll-snap-align: center;
    min-width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: 800;
    /* font-size: 25em; */
    will-change: transform, opacity;
    text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030;
}

.controls {
    position: absolute;
    bottom: 16px;
    right: 16px;
    z-index: 2;
    display: flex;
}

.button {
    position: relative;
    /* padding: 24px; */
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    font-size: 24px;
    font-weight: 700;
    z-index: 2;
    height: 56px;
    width: 56px;
    margin: 0px 0px 0 16px;
    /* margin: 1em 0.8em; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.button.type1 {
    color: #fff;
}

.button.type1.type1::after,
.button.type1.type1::before {
    content: '';
    display: block;
    position: absolute;
    width: 20%;
    height: 20%;
    border: 2px solid;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    border-radius: 2px;
}

.button.type1.type1::after {
    bottom: 0;
    right: 0;
    border-top-color: transparent;
    border-left-color: transparent;
    border-bottom-color: #fff;
    border-right-color: #fff;
}

.button.type1.type1::before {
    top: 0;
    left: 0;
    border-bottom-color: transparent;
    border-right-color: transparent;
    border-top-color: #fff;
    border-left-color: #fff;
}

.button.type1.type1:hover:after,
.button.type1.type1:hover:before {
    width: 100%;
    height: 100%;
}
